@import url(https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300;500&display=swap);
body {
    margin: 0;
    padding: 0;
    background: #242c3D;
}


.App {

}

.Background {
    background: #c3375E;
    width: 64vw;
    height: 100vh;
}

.Logo {
    position: absolute;
    top: 64px;
    bottom: 64px;
    left: 64px;
    height: 48px;
}

.Header {
    position: absolute;
    top: 64px;
    bottom: 64px;
    left: 64px;
    right: 50vw;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
}

.Header-Content {
    margin-right: 64px;
}

h1 {
    margin: 0;
    color: #242C3D;
    font-family: 'HelveticaNeue-CondensedBold', 'Helvetica Neue Condensed Bold', 'Helvetica Neue', 'Barlow Condensed', Helvetica;
    font-size: 48px;
    font-weight: 700;
    font-stretch: condensed;
    line-height: 40px;
}

p {
    margin: 0;
    margin-top: 24px;
    color: #242C3D;
    font-family: 'Helvetica Neue', 'Raleway', Helvetica;
    font-size: 18px;
    font-weight: 300;
    line-height: 28px;
}

@media only screen and (max-width: 768px) {
    .Header {
        display: none;
    }

    .Logo {
        top: 24px;
        bottom: 24px;
        left: 24px;
    }

    .Background {
        height: 36vh;
        width: 100vw;
    }
}

.Board {
    position: absolute;
    top: 64px;
    bottom: 64px;
    right: 64px;
    left: 50vw;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: stretch;
            align-items: stretch;
}

.Board-Panel {
    margin: auto;
    width: 100%;
    height: 100%;;
    display: -webkit-flex;;
    display: flex;
    -webkit-flex-grow: 0.5;
            flex-grow: 0.5;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: stretch;
            align-items: stretch;
}

.Board-Space {
    height: 48px;
}

.Board-Panel-Editor {
    height: 100%;
    display: -webkit-flex;
    display: flex;
    border-radius: 12px 12px 0 0;
    background: #ffffff;
}

.Board-Panel-Separator {
    height: 1px;
    background: #e9ecf1;
}

.Board-Panel-Toolbar {
    height: 64px;
    display: -webkit-flex;
    display: flex;
    border-radius: 0 0 12px 12px;
    background: #ffffff;
}

.Board-Panel-Toolbar-Button {

}

textarea {
    width: 100%;
    border: 0;
    outline: none;
    padding: 24px;
    margin: 0;
    background: none;
    resize: none;
    color: #50596a;
    font-family: 'Helvetica Neue', 'Raleway', Helvetica;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
}

textarea::-webkit-input-placeholder {
    color: #242c3d;
    opacity: 0.38;
}

textarea:-ms-input-placeholder {
    color: #242c3d;
    opacity: 0.38;
}

textarea::placeholder {
    color: #242c3d;
    opacity: 0.38;
}

.Board-Panel-Toolbar {
    min-height: 64px;
}

.Board-Panel-ButtonGroup {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: right;
            align-items: right;
    margin-left: auto;
    margin-right: 8px;
}

.Board-Panel-Button {
    margin: 8px;
    margin-left: auto;
    padding: 0;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 48px;
    background: transparent;
    transition: 0.48s;
}

.Board-Panel-Button:hover {
    background: #f4f4f6;
    transition: 0.32s;
}

.Board-Panel-Button:active {
    background: #ececef;
    transition: 0.08s;
}

.Icon {
    margin: 0;
    width: 16px;
    height: 16px;
    padding: 16px;
}

.Board-Panel-Switcher {
    display: -webkit-flex;
    display: flex;
    margin-left: 28px;
}

.Board-Panel-Switcher-Label {
    margin: auto;
    margin-right: 8px;
    padding: 0;
    color: #242c3d;
    font-family: 'Helvetica Neue', Helvetica;
    font-size: 12px;
    font-weight: 500;
    line-height: 24px;
    text-transform: uppercase;
}

.Board-Panel-Switcher-Button {
    border: none;
    outline: none;
    background: transparent;
    margin: auto;
    padding: 0;
    cursor: pointer;
    color: #c3375e;
    font-family: 'Helvetica Neue', 'Raleway', Helvetica;
    font-size: 12px;
    font-weight: 500;
    line-height: 24px;
    text-transform: uppercase;
}

.Board-Panel-Counter {
    display: -webkit-flex;
    display: flex;
    margin-left: 28px;
}

.Board-Panel-Counter-Label {
    margin: auto;
    margin-right: 8px;
    padding: 0;
    color: #242c3d;
    font-family: 'Helvetica Neue', 'Raleway', Helvetica;
    font-size: 12px;
    font-weight: 500;
    line-height: 24px;
    text-transform: uppercase;
}

.Board-Tooltip {
    padding: 4px 12px 4px 12px !important;
    color: #ffffff !important;
    font-family: 'Helvetica Neue', 'Raleway', Helvetica;
    font-size: 12px !important;
    font-weight: 300;
    line-height: 24px;
    text-transform: uppercase;
}

@media only screen and (max-width: 768px) {
    .Board {
        top: 96px;
        bottom: 24px;
        left: 24px;
        right: 24px;
    }
}

